.aibp-back-to {
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #EBEBEB;
}
.aibp-back-to .aibp-back-to-left {
  float: left;
  display: flex;
}
.aibp-back-to .aibp-back-to-left .aibp-back-to-icon > a {
  color: #333;
}
.aibp-back-to .aibp-back-to-left .aibp-back-to-icon.aibp-back-to-icon-button > a {
  padding: 6px 8px;
  border: 1px solid #EBEBEB;
}
.aibp-back-to .aibp-back-to-left .aibp-back-to-text {
  font-size: 14px;
  color: #333;
  margin-left: 5px;
}
.aibp-back-to .aibp-back-to-left .aibp-back-to-text > a {
  color: #333;
}
.aibp-back-to .aibp-back-to-left .aibp-back-to-title,
.aibp-back-to .aibp-back-to-left .aibp-back-to-button-group {
  margin-left: 20px;
}
.aibp-back-to .aibp-back-to-left .aibp-back-to-title .aibp-back-to-button:not(:first-child),
.aibp-back-to .aibp-back-to-left .aibp-back-to-button-group .aibp-back-to-button:not(:first-child) {
  margin-left: 10px;
}
.aibp-back-to .aibp-back-to-main .aibp-back-to-title-center {
  text-align: center;
}
.aibp-back-to .aibp-back-to-main .aibp-back-to-title-right {
  text-align: right;
}
.aibp-back-to .aibp-back-to-title {
  font-size: 18px;
  color: #333;
}
