.aibp-expand-icon {
  display: inline-block;
  margin-left: 8px;
  vertical-align: middle;
}
.aibp-expand-icon .expand-arrow {
  margin-left: 5px;
}
.aibp-expand-icon .expand-arrow svg,
.aibp-expand-icon .expand-arrow::after {
  transition: transform 0.48s;
}
.aibp-expand-icon.aibp-expand-icon-active .expand-arrow svg,
.aibp-expand-icon.aibp-expand-icon-active .expand-arrow::after {
  transform: rotate(-180deg);
}
