.aibp-bottom-submit {
  position: fixed;
  height: 60px;
  bottom: 0;
  left: 0;
  right: 0;
  padding-left: calc(100px + 190px);
  line-height: 60px;
  background: #fff;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.1);
  transition: all 0.2s;
}
.aibp-bottom-submit .ant-btn:not(:first-child) {
  margin-left: 10px;
}
