.aibp-uploader-container {
  border: 1px solid #EBEBEB;
  width: 520px;
  height: auto;
}
.aibp-uploader-container .aibp-uploader {
  width: 100%;
  min-height: 270px;
  overflow-y: auto;
  padding-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}
.aibp-uploader-container .aibp-uploader.aibp-uploader-card {
  padding-left: 20px;
  padding-right: 20px;
  align-items: flex-start;
  flex-direction: initial;
  flex-wrap: wrap;
}
.aibp-uploader-container .aibp-uploader .uploader-action {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
}
.aibp-uploader-container .aibp-uploader .uploader-action-card {
  align-items: flex-start;
}
.aibp-uploader-container .aibp-uploader .cloud-img {
  width: 62px;
  margin-bottom: 20px;
}
.aibp-uploader-container .aibp-uploader .uploader-extra {
  color: #999;
  text-align: center;
  margin-top: 20px;
}
.aibp-uploader-container .aibp-uploader .uploader {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.aibp-uploader-container .aibp-uploader .uploader .ant-upload {
  margin: 0 auto;
}
.aibp-uploader-container .aibp-uploader .uploader .ant-btn {
  width: 168px;
  height: 40px;
}
.aibp-uploader-container .aibp-uploader .uploader .continue-upload {
  border: 1px solid #EBEBEB;
  height: 80px;
  width: 80px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card {
  width: 80px;
  height: 80px;
  position: relative;
  border: 1px solid #EBEBEB;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  cursor: pointer;
  margin-right: 20px;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card.uploader-card-progress-none {
  align-items: center;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card.uploader-card-progress-none > img {
  transform: translateY(0);
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card.uploader-card-progress-has {
  align-items: flex-end;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card.uploader-card-progress-has > img {
  transform: translateY(6px);
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .custom-icon {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
  width: 24px;
  height: 24px;
  font-size: 24px;
  z-index: 10;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .custom-icon .anticon-check-circle {
  color: #5FB333;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .custom-icon .anticon-close-circle {
  color: #108cee;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .custom-icon .anticon-exclamation-circle {
  color: #e73135;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card > img {
  height: 50px;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .img-box {
  width: 80px;
  height: 50px;
  margin-bottom: -12px;
  overflow: hidden;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .img-box img {
  width: 80px;
  transform: translateY(-12px);
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .img-box.no-progress {
  margin-bottom: 0;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .ant-upload-list-item-name {
  display: none;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .ant-progress.ant-progress-line {
  width: 80%;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .ant-progress .ant-progress-outer {
  padding: 0;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .ant-progress .ant-progress-outer .ant-progress-bg {
  height: 4px !important;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .ant-progress .ant-progress-text {
  display: none;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .uploader-card .ant-progress .ant-progress-inner {
  height: auto;
}
.aibp-uploader-container .aibp-uploader .uploader-card-list .card-name {
  width: 80px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin: 3px 0 20px 0;
  font-size: 12px;
  text-align: center;
}
