.aibp-detail-content-sidebar {
  width: 140px !important;
  height: 100%;
  float: left;
  position: relative;
  z-index: 1000;
  background: #F5F5F5;
}
.aibp-detail-content-sidebar.ant-menu-inline .ant-menu-item:first-child {
  margin-top: 0;
}
.aibp-detail-content-sidebar.ant-menu-inline .ant-menu-item::after {
  left: 0;
  right: initial;
  border-right: 3px solid #108cee;
}
.aibp-detail-content-sidebar.ant-menu-inline .ant-menu-item.ant-menu-item-selected {
  background-color: #fff !important;
}
