.aibp-grid-view-item {
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid #EBEBEB;
}
.aibp-grid-view-item .aibp-grid-view-item-title {
  font-size: 16px;
  color: #000;
  line-height: 30px;
  margin-bottom: 20px;
}
.aibp-grid-view-item .aibp-grid-view-item-title::before {
  content: ' ';
  display: inline-block;
  width: 4px;
  height: 16px;
  margin: 7px 10px 7px 0;
  vertical-align: top;
  background-color: #108cee;
}
.aibp-grid-view-item .aibp-grid-view-item-row {
  line-height: 30px;
}
.aibp-grid-view-item .aibp-grid-view-item-row:not(:last-child) {
  margin-bottom: 20px;
}
.aibp-grid-view-item .aibp-grid-view-item-row .aibp-grid-view-item-col {
  display: flex;
}
.aibp-grid-view-item .aibp-grid-view-item-row .aibp-grid-view-item-col .grid-view-item-label {
  color: #666;
}
.aibp-grid-view-item .aibp-grid-view-item-row .aibp-grid-view-item-col .grid-view-item-content {
  color: #333;
}
